// item-process styles
.m-item-process{
    &__items{
        display: inline-flex;
        flex-wrap: wrap;
        
    }
    &__item{
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        margin-right:100px;
        text-align: justify;
        width: 170px;
        position: relative;
        
        &:first-child{
            order: 1;
            margin-left: 20px;
            
        }
        &:nth-child(2){
            order:5 ;
            margin-left: 150px;
            
        }
        &:nth-child(3){
            order:2 ;
        }
        &:nth-child(4){
            order: 6;
        }
        &:nth-child(5){
            order:3 ;
        }
        &:nth-child(6){
            order: 7;
        }
        &:nth-child(7){
            order:4 ;
        }
        &:last-child{
            order: 8;
            margin-right: 0;
            & .m-item-process__icon{
                display: none;
            }
        }
        &:nth-child(even){
            & .m-item-process__icon{
                position: absolute;
                top: -190px;
                left: 115px;
                height: 200px;
                width: 20px;
                transform: rotate(205deg);
                opacity: .5;
                fill: $c-violet-2;
            }
            
        }
    }
    &__icon{
        position: absolute;
        top: 125px;
        left: 160px;
        height: 200px;
        width: 20px;
        transform: rotate(-25deg);
        opacity: .5;
        fill: $c-violet-2;
  
    }
    &__img{
        height: 160px;
        width: 160px;
        border: solid 2px $c-violet;
        @include backgroundCoverCenter;          
        border-radius: 100%;
        padding-bottom: 10px;
        position: relative;
        z-index: 1;
    }
    &__subtitle{
        font-size:$fs-16;
        hyphens: auto;
        line-height: $lh-1-5;
        letter-spacing: 0.8px;
        color: $c-black-2;
    }

}