// item-advantage styles
.m-item-advantage{
    padding-top: 50px;
    &__items{
        display: inline-flex;
        flex-wrap: wrap;
        align-items: center
    }
    &__item{
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        padding-top: 50px;
    }
    &__subtitle{
        padding-top: 75px;  
        font-size: $fs-18;
        line-height: $lh-1-3;
        text-align: center;
        color: $c-white-10;
        width: 75%;
    }
    &__icon{
        height: 115px;
        width: auto;
        fill: $c-white-6;
    }
}